body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  min-height: 100vh;
  background-image: -webkit-gradient(linear,left top, left bottom,from(rgba(119,102,255,0)),to(rgba(119,102,255,.15))),-webkit-gradient(linear,left top, left bottom,from(#000),to(#000));
  background-image: -webkit-linear-gradient(top,rgba(119,102,255,0),rgba(119,102,255,.15)),-webkit-linear-gradient(#000,#000);
  background-image: -o-linear-gradient(top,rgba(119,102,255,0),rgba(119,102,255,.15)),-o-linear-gradient(#000,#000);
  background-image: linear-gradient(180deg,rgba(119,102,255,0),rgba(119,102,255,.15)),linear-gradient(#000,#000);
  color: white;
}

body::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	        box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #2b2552;
}

body::-webkit-scrollbar
{
	width: 6px;
	background-color: #F5F5F5;
}

body::-webkit-scrollbar-thumb
{
	background-color: #0f0e23;
}


