.GameThumbnail {
  -ms-flex: 1 1 16em;
      flex: 1 1 16em;
  margin: 0.4em;
  height: 10em;
  border: 1px solid black;
}

#sidebarToggle:checked ~ .GameList .GameThumbnail {
  height: 10em;
}

.GameThumbnailContainer {

  position: relative;

  width: 100%;
  height: 100%;

  overflow: hidden;
}

.GameThumbnailPreload {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  color: white;
  font-size: 1em;
  font-weight: bold;
  opacity: 0.8;
}

.GameThumbnailImageLoaded {

  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  opacity: 0;

  -webkit-transition: opacity 1500ms ease;

  -o-transition: opacity 1500ms ease;

  transition: opacity 1500ms ease;
}

.colorBackground {
  -webkit-box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.68);
          box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.68);
}

.GameThumbnailVideo {
  position: absolute;
  z-index: 4;
  top: 0;
  left: 50%;
  right: 50%;
  margin-left: auto;
  margin-right: auto;
  -webkit-transform: translate(-50%, 0%);
      -ms-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);

  opacity: 0;
  -webkit-transition: opacity 800ms ease;
  -o-transition: opacity 800ms ease;
  transition: opacity 800ms ease;
  
  height: 100%;
}

.show {
  opacity: 1;
}

.hide {
  opacity: 0;
}

@media (max-width: 1230px)  { 
  .GameThumbnail {
    height: 13em;
  }

  #sidebarToggle:checked ~ .GameList .GameThumbnail {
    height: 9em;
  }
}

@media (max-width: 1155px)  {  
  .GameThumbnail,  #sidebarToggle:checked ~ .GameList .GameThumbnail {
    height: 12em;
  }
}

@media (max-width: 970px)  {  
  .GameThumbnail, #sidebarToggle:checked ~ .GameList .GameThumbnail {
    height: 10em;
  }
}

@media (max-width: 891px)  { 

  .GameThumbnail, #sidebarToggle:checked ~ .GameList .GameThumbnail {
    height: 13em;
  }
}

@media (max-width: 780px)  { 
  .GameThumbnail, #sidebarToggle:checked ~ .GameList .GameThumbnail {
    height: 12em;
  }
}

@media (max-width: 700px)  { 
  .GameThumbnail, #sidebarToggle:checked ~ .GameList .GameThumbnail {
    height: 11em;
  }
}

@media (max-width: 660px)  { 
  .GameThumbnail, #sidebarToggle:checked ~ .GameList .GameThumbnail {
    height: 10em;
  }
}

@media (max-width: 616px)  { 
  .GameThumbnail, #sidebarToggle:checked ~ .GameList .GameThumbnail {
    height: 20em;
    -ms-flex: 1 1 100%;
        flex: 1 1 100%;
    margin: 0.5em 0em;
  }
}

@media (max-width: 540px)  { 
  .GameThumbnail, #sidebarToggle:checked ~ .GameList .GameThumbnail {
    height: 17em;
  }
}

@media (max-width: 400px)  { 
  .GameThumbnail, #sidebarToggle:checked ~ .GameList .GameThumbnail {
    height: 13em;
  }
}

@media (max-width: 320px)  { 
  .GameThumbnail, #sidebarToggle:checked ~ .GameList .GameThumbnail {
    height: 11em;
  }
}


